/* ===================================
========= register page  */
.form-container {
  margin-top: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
  flex-direction: column;
  background-color: #ffdab9;
  background-image: linear-gradient(0deg, #ffdab9 0%, #ff8a5b 100%);
}

.form-container form {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 20px;
  background-color: #fff;
}

.form-container form .title {
  text-align: center;
  margin-bottom: 15px;
  font-weight: bold;
  font-family: "Playfair Display", serif;
  color: #ff8a5b;
}

.form-container form input {
  border: none;
  border-bottom: 1px solid #ff8a5b;
  border-radius: 0;
}

.form-container form button {
  border: 1px solid #ff8a5b;
  border-radius: 25px;
  background-color: #ff8a5b;
  color: white;
  width: 250px;
}

.form-container form button:hover {
  background-color: #ff5733;
  border: 1px solid #ff5733;
}

.forgot-btn {
  background-color: #ff8a5ba4 !important;
  border: 0 !important;
}

.forgot-btn:hover {
  color: white !important;
}
