.banner-img {
  margin-top: 60px;
  }
  
  .home-page h1 {
  color: #333;
  font-family: "Playfair Display", serif;
  font-size: 32px;
  margin-bottom: 40px;
  }
  
  .home-page {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 40px;
  }
  
  .home-page .card {
  background-color: #fff;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  width: 100%;
  margin-bottom: 40px;
  transition: all 0.3s ease-in-out;
  }
  
  .home-page .card:hover {
  transform: translateY(-5px);
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);
  }
  
  .home-page .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 20px;
  border-bottom: 1px solid #f0f0f0;
  }
  
  .home-page .card-price {
  color: #33cc33;
  font-weight: bold;
  font-size: 24px;
  }
  
  .home-page button {
  background-color: #ff6600;
  border: none;
  border-radius: 0;
  width: 100%;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  transition: all 0.3s ease-in-out;
  }
  
  .home-page button:hover {
  background-color: #cc5200;
  cursor: pointer;
  }
  
  .home-page .card-text {
  color: #666;
  font-size: 16px;
  padding: 20px;
  }
  
  .home-page .card img:hover {
  transform: scale(1.05);
  }
  
  .home-page .card-img-top {
  height: 300px !important;
  object-fit: cover;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  }
  
  /* ==================
  filters
  ============== */
  
  .filters h4 {
  margin-top: 100px;
  color: #333;
  font-size: 24px;
  font-weight: bold;
  border-bottom: 1px solid #f0f0f0;
  padding-bottom: 10px;
  margin-bottom: 40px;
  }
  
  .ant-checkbox-wrapper:first-of-type {
  margin-left: 8px;
  }
  
  .ant-checkbox-wrapper {
  margin: 2px;
  }
  
  .ant-radio-wrapper {
  margin-bottom: 5px !important;
  margin-left: 10px;
  }
  
  .filters button {
  background-color: #ff6600;
  border: none;
  border-radius: 0;
  width: 100%;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  margin-top: 40px;
  transition: all 0.3s ease-in-out;
  }
  
  .filters button:hover {
  background-color: #cc5200;
  cursor: pointer;
  }
  
  .loadmore {
  color: #33cc33;
  font-weight: bold;
  font-size: 20px !important;
  margin-top: 40px;
  }