.category {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 80px;
}

.category .card {
  width: calc(33.33% - 10px);
  margin-bottom: 20px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.15);
  min-height: 400px;
}

.category .card:hover {
  transform: translateY(-5px);
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.2);
}

.category .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}

.category .card-name-price h5 {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
}

.category .card-price {
  color: #00b894;
  font-weight: bold;
  font-size: 18px;
}

.category button {
  border: none;
  border-radius: 5px;
  background-color: #fdcb6e;
  color: #fff;
  font-size: 14px;
  font-weight: bold;
  padding: 10px;
  cursor: pointer;
}

.category button:hover {
  background-color: #ff9f43;
}

.category .card-text {
  padding: 10px;
  color: #636e72;
  height: 100px;
  overflow: hidden;
}

.category .card img:hover {
  transform: scale(1.05);
  transition: transform 0.2s ease-in-out;
}

.category .card-img-top {
  height: 250px;
  object-fit: cover;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
